<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder="请输入内容"
            clearable
            v-model="queryInfo.query"
            @clear="getGoodsList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="handleCurrentChange(1);getGoodsList;"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddPage">添加商品</el-button>
        </el-col>
      </el-row>

      <el-table :data="goodslist" stripe border style="width: 100%">
        <el-table-column type="index"> </el-table-column>
        <el-table-column prop="commname" label="商品名称"></el-table-column>
        <el-table-column
          prop="thinkmoney"
          label="商品价格（元）"
          width="95px"
        ></el-table-column>
        <el-table-column
          prop = 'commstatus'
          label="状态"
          width="70px"
        >
          <template v-slot="scope">
            <el-switch
              v-model="scope.row.commstatus"
              @change="StateChanged(scope.row)"
            ></el-switch>
          </template></el-table-column>
        <el-table-column prop="updatetime" label="创建时间" width="170px">
          <template v-slot="scope">
            {{ scope.row.updatetime  }}
          </template>
        </el-table-column>
        <el-table-column label="操作" width="130px">
          <template v-slot="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
             
            ></el-button>
            <el-button
              size="mini"
              type="warning"
              icon="el-icon-delete"
              @click="removeById(scope.row.commid)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 20, 50]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 10
      },
      goodslist: [],
      total: 100
    };
  },
  methods: {
    async getGoodsList() {
      this.$axios.get('http://8.141.56.170:9100/admin/commodity/100',{params:{
        limit:this.queryInfo.pagesize,
        page:this.queryInfo.pagenum
      }}).then(res=>{
        let goodl = res.data.data
        for(var i = 0;i<goodl.length;i++){
              if(goodl[i].commstatus){
                goodl[i].commstatus = true
              }
              else{goodl[i].commstatus = false}
        }
        this.goodslist = res.data.data  
       
      })
      
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getGoodsList();
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getGoodsList();
    },
    removeById(id) {
    id //删除商品
    },
    goAddPage() {
      this.$router.push("addGoods");
    },
    StateChanged(val){
    const key = val.commstatus?1:0
      this.$axios.put('http://8.141.56.170:9100/admin/changecommstatus/'+val.commid+'/'+key).then(res=>{
     
        console.log(res)
        
      })
     
    },
  } ,
  created() {
    this.getGoodsList();
  }
};
</script>

<style lang="less" scoped></style>
